@import '../../../../../../../includes/assets/css/consts.scss';

.dp-button-cancel {
	background: url('./../../../../../icons/dp-close-cross-light.svg');
	margin-left: 0;
	background-position: center;
	background-size: 45%;
	background-repeat: no-repeat;
	border-radius: 2px;
	width: 48px;
	height: 48px;
	background-color: rgba(var(--dpu-color-r), 0.9);
	padding: 0;
	cursor: pointer;
	transition: all ease-out $transition-time-med;

	@include dpitBoxShadowOff0(4px);
	border: none;
	outline: none;
	opacity: 1;


	@include atXXS {
		top: 20px;
		right: 21px;
	}

	&--alt {
		transform: none;
		border-radius: 50%;
		background-color: rgba(var(--dpu-color-r), 0.9);
	}
}