@import './../../../../../../../includes/assets/css/consts.scss';

.dp-select {

	// native select
	&__native {
		background: url('./../../../../../icons/dp-select_arrow-down-dark.svg') calc(100% - 12px) 10px no-repeat, $background-color-light !important;
		@include panelControlItemHovFoc($transition-time-fast);
		border-radius: 2px !important;
		@include primaryFontImportant(300, $text-color-dark, 18px, 1.167, normal, left);
		padding: 9px 16px 11px 16px;
		min-height: 40px;
		appearance: none;
		background-size: 17px 17px, auto !important;
		outline: 0;

		option {
			@include atXS {
				font-size: 15px !important;
			}
		}
	}

	&--disabled {
		opacity: 0.4;
		pointer-events: none;
	}

	// custom select react-select
	&__control {
		background-color: $background-color-light;
		@include panelControlItemHovFoc($transition-time-fast);
		padding-left: 8px;
		padding-right: 8px;
		border: none !important;

		@include atXS {
			margin-top: -5px;
		}
	}

	&__menu {
		border-radius: 2px;

	}

	&__menu-list {

		border-radius: 2px;
		padding-top: 0 !important;
		padding-bottom: 0 !important;
		box-shadow: 0 0 3px 0 rgba(0, 0, 0, 0.1), 0 0 12px 0 rgba(0, 0, 0, 0.25);
	}

	&__indicator-separator {
		display: none;
	}

	&__dropdown-indicator {

		//pointer-events: none;
		padding: 0px !important;

		.dp-indicator {
			cursor: pointer !important;
			background-position: center;
			background-size: 80%;
			background-repeat: no-repeat;
			width: 24px;
			height: 24px;

			&--opened {
				background-image: url('./../../../../../icons/dp-close-cross-dark.svg');
			}

			&--closed {
				background-image: url('./../../../../../icons/dp-select_arrow-down-dark.svg');
			}
		}
	}

	&__single-value {

		opacity: 1;

		&:disabled {
			opacity: 0.5;
		}

		transition: opacity $transition-time-fast;
		@include primaryFont(600, $text-color-dark, 14px, 21px, normal, left);
		text-transform: uppercase !important;
	}

	&__placeholder {
		@include primaryFont(300, $text-color-placeholder, 14px, 21px, normal, left);
		text-transform: uppercase !important;
	}

	&__input {
		@include primaryFont(400, $text-color-dark, 14px, 21px, normal, left);
		text-transform: uppercase !important;

		input {
			@include primaryFontImportant(400, $text-color-dark, 14px, 21px, normal, left);
			height: unset !important;
			margin-bottom: 0 !important;

			&:focus {
				border: none !important;
				box-shadow: none !important;
			}
		}
	}

	&__option {


		padding: 10px 20px !important;
		overflow: hidden;
		@include primaryFont(300, $text-color-dark, 14px, 21px, normal, left);
		background-color: transparent;

		&:first-child {
			padding-top: 15px !important;
		}

		&:last-child {
			padding-bottom: 12px !important;
		}


		&--is-selected //&:hover

			{
			font-weight: 600;
			color: rgba(var(--dpu-color-2-text), 1);
			background-color: rgba(var(--dpu-color-2), 0.5);
		}

		&--is-focused {
			font-weight: 600;
			color: rgba(var(--dpu-color-2-text), 1);
			background-color: rgba(var(--dpu-color-2), 0.8);
		}

		&:active {
			color: rgba(var(--dpu-color-2-text), 1);
			background-color: rgba(var(--dpu-color-2), 0.9);
		}

		@include atXS {
			padding: 5px 15px !important;

			&:first-child {
				padding-top: 10px !important;
			}

			&:last-child {
				padding-bottom: 8px !important;
			}
		}
	}


	.windows &__menu-list {
		&::-webkit-scrollbar {
			width: 19px;
		}

		&::-webkit-scrollbar-thumb {
			border: 6px solid rgba(0, 0, 0, 0);
			border-width: 40px 8px 40px 5px;
			background-clip: padding-box;
			border-radius: 7px;
			background-color: rgba(0, 0, 0, 0.15);
			box-shadow: inset -1px -1px 0px rgba(0, 0, 0, 0.05), inset 1px 1px 0px rgba(0, 0, 0, 0.05);
		}

		&::-webkit-scrollbar-button {
			width: 0;
			height: 0;
			display: none;
		}

		&::-webkit-scrollbar-track {
			border-radius: 10px;
		}

		&::-webkit-scrollbar-corner {
			background-color: transparent;
		}
	}

	// resty is in js 
}