@import '../../../../../../../includes/assets/css/consts.scss';

.dp-step-ext-cont {

	@include activeTransitionFade(flex);
	background: linear-gradient(315deg, rgba(var(--dpu-color-2-text), 0.88) 25%, rgba(var(--dpu-color-2-text), 0.95) 100%);
	padding: 15px 20px 5px;
	border-radius: 2px;
	pointer-events: all;
	align-items: flex-end;
	flex-direction: column;
	box-shadow: 0 0 6px 0 rgba(var(--dpu-color-g), 0.5);

	@include atTouchDev {
		padding: 8px 20px 5px;

	}

	@include atXS {
		align-items: flex-start;
	}

	@include atXXS {
		max-width: calc(100vw - 90px);
	}


	&__item {
		display: flex;
		align-items: center;
		justify-content: center;
		margin-bottom: 10px;

		@include atXXXS {
			flex-direction: column;
			align-items: flex-start;
		}

		&__label {
			color: rgba(var(--dpu-color-2), 1);
			margin-right: 10px;
			font-size: 14px;
			white-space: nowrap;
			text-transform: uppercase;

			@include atXXXS {
				font-size: 12px;
				margin-right: 0;
			}
		}

		&__number {
			max-width: 96px;
		}



		.toggleContainer {
			.toggle.on {
				background: rgba(var(--dpu-color-g), 1) !important;

			}

			.toggleOption {
				&.selected {
					color: rgba(var(--dpu-color-g-text), 1);
				}
			}
		}
	}

	&__arrow-up {



		margin-bottom: 8px;
		margin-right: -10px !important;
		margin-left: 0 !important;
		border-radius: 50%;
		box-shadow: 0px 0px 5px 0px rgba(var(--dpu-color-2), .3);
		padding: 4px;
		display: none;

		@include atTouchDev {
			display: block;
		}

		margin-right: 5px;

		@include atXS {
			margin-left: -10px !important;
			margin-right: 0px !important;
		}



		@include atXS {
			align-self: flex-start !important;
			margin-right: 0;
			margin-left: -10px;
		}

		path {
			stroke: rgba(var(--dpu-color-2), 1) !important;
		}


	}
}