@import '../../../../../../../includes/assets/css/consts.scss';

@mixin active {
	//transform: translate(0, 0);
	margin-right: 0;
	opacity: 1;
}

@mixin inactive {
	//transform: translate(100%, 0);
	opacity: 0;
	margin-right: -68px;
}

.dp-multi-container {
	position: relative;
	//z-index: 2147483644;
	display: none;
	min-width: 68px;
	min-height: 48px;
	transition: all $transition-time-fast ease-in;
	@include inactive;

	&__menu {
		position: absolute;
		top: 0;
		right: 0;
		padding: 10px 10px 10px;
		pointer-events: all;
		display: flex;
		flex-direction: column;
		align-items: flex-end;
		transition: transform $transition-time-med ease-in;
		transform: rotate(-90deg);
		transform-origin: calc(100% - 34px) 34px;

		&--opened {
			transform: none;
		}

	}



	&--active {
		display: block;
		@include active;

		&.dp-multi-container--in-transition {
			@include inactive;
		}
	}

	&--slide-down {
		width: 100%;
		min-height: 28px;
		margin-right: 0;

		.dp-multi-container__menu {
			transform-origin: unset;
			padding: 0;
			display: flex;
			width: auto;
			transition: margin $transition-time-fast ease-in;
			transform: none;


			@include atXS {
				right: unset;
				left: 0;
			}

		}

		&.dp-multi-container--active {
			&.dp-multi-container--in-transition {
				opacity: 0;
				margin-right: 0;
				margin-top: -20px;
			}

			.dp-multi-container__menu--opened {
				margin-top: -20px;
			}
		}


	}

	&__item {
		width: auto;
		height: auto;
	}
}