@import '../../../../../../../includes/assets/css/consts.scss';

@mixin active {

	&.margin-based-transition {
		margin-right: 0;
	}

	opacity: 1;
	pointer-events: all;

	&.animate {
		animation: pulse 1s;
	}

	&:hover {
		opacity: 0.9;
		@include dpitBoxShadowAlfa(0.15);
	}

}

@mixin inactive {

	transform: translateX(100%);

	&.margin-based-transition {
		transform: none;

		&.margin-based-transition-80 {
			margin-right: -80px;
		}

		&.margin-based-transition-120 {
			margin-right: -120px;
		}

		&.margin-based-transition-160 {
			margin-right: -170px;
		}

		&.margin-based-transition-250 {
			margin-right: -250px;
		}

		&.margin-based-transition-350 {
			margin-right: -350px;
		}

		@include atXS {
			margin-right: -167px;

			&.margin-based-transition-80 {
				margin-right: -60px;
			}

			&.margin-based-transition-120 {
				margin-right: -84px;
			}

			&.margin-based-transition-160 {
				margin-right: -119px;
			}

			&.margin-based-transition-250 {
				margin-right: -175px;
			}

			&.margin-based-transition-350 {
				margin-right: -300px;
			}
		}
	}

	opacity: 0;
}

.dp-controls {

	pointer-events: none;
	height: 100%;
	width: 100%;

	&__wrap {
		//z-index: 2147483644;
		@include z-index(13);
		position: fixed;
		bottom: 0;
		right: 0;
		display: flex;
		align-items: flex-end;
		transition: all 0.5s ease;
		padding: 0 50px 30px;

		@include atM {
			padding: 0 40px 20px;
			flex-wrap: wrap-reverse;
			align-items: flex-start;
			justify-content: flex-end;
			margin-left: auto;
		}

		@include atXS {
			padding: 0 20px 10px;
			justify-content: center;
		}
	}

	&__multi-close {
		position: fixed;
		top: 0px;
		right: 0px;
		margin: 25px 40px 0;
		@include z-index(13);
		//z-index: 2147483644;

		&--bottom-left {
			top: unset;
			right: unset;

			left: 0px;
			bottom: 40px;


			.dp-multi-container__menu {
				transform-origin: 34px 34px;
				right: unset;
				left: 0;
				align-items: center;
				flex-direction: row;



				@include atXS {
					width: 100vw;
					flex-wrap: wrap;

					&--opened {
						transform: translateY(-50%);
					}

					//transform-origin: calc(100% - 34px) 34px;
				}

			}

			.dp-multi-container__item:not(:first-child) {
				margin-left: 10px;
			}

			.dp-controls__button--cancel-sub {
				text-align: left;
				margin-top: 5px;
				margin-bottom: 5px;

				@include atXS {
					min-width: 100px;
				}
			}

		}


		@include atM {
			margin: 20px 20px 0;
		}

		@include atXS {
			margin: 10px 10px 0px;
		}
	}

	&__button {
		@include primaryFont(500, $text-color-light, 18px, 1.5);
		text-transform: uppercase;
		pointer-events: none;
		cursor: pointer;
		justify-content: center;
		align-content: center;
		padding: 10px 20px 10px;
		border-radius: 2px;
		opacity: 0;
		@include transitionMulti($transition-time-med, ease-out, color, background-color, transform, box-shadow, opacity, margin-right);
		@include dpitBoxShadow;

		outline: none;
		border: none;
		//z-index: 2147483644;
		@include z-index(13);
		color: rgba(var(--dpu-color-2-text), 1);

		background-color: rgba(var(--dpu-color-2), 1);
		display: none;
		margin-left: 10px;
		margin-bottom: 10px;
		text-align: center;

		&:first-child {
			margin-left: 0px;
		}

		span {
			opacity: 0;
			transition: all 0.3s ease;
		}

		&--edit-step {
			color: rgba(var(--dpu-color-b-text), 1);
			background-color: rgba(var(--dpu-color-b), 1);
			font-family: 'dashicons';
		}

		&--change-selection {
			color: rgba(var(--dpu-color-b-text), 1);
			background-color: rgba(var(--dpu-color-b), 1);
		}

		&--pause-builder {
			position: fixed;
			bottom: 0;
			left: 50%;
			transform: translateX(-50%);
			border-radius: 2px;
			color: rgba(var(--dpu-color-g-text), 1);
			background-color: rgba(var(--dpu-color-g), 0.9);
		}

		&--save {
			color: rgba(var(--dpu-color-g-text), 1);
			background-color: rgba(var(--dpu-color-g), 1);
			position: relative;
		}

		&--alt {
			border-radius: 2px;
		}

		&--active:not(.dp-controls__button--cancel-sub) {
			display: flex;

			@include active;

			&.dp-controls__button--in-transition {
				@include inactive;
			}
		}

		&--add-first-step {
			position: fixed;
			align-items: center;
			justify-content: center;
			font-size: 25px;
			min-height: 100px;
			left: calc(50% - 175px);
			top: calc(50% - 50px);
			background-color: rgba(var(--dpu-color-g), 1);
			color: rgba(var(--dpu-color-g-text), 1);
			border-radius: 2px;
			text-transform: uppercase;

			@include atXS {
				left: calc(50% - 150px);
				min-height: 70px;
				top: calc(50% - 35px);
			}

		}



		&.margin-based-transition-120 {
			min-width: 120px;
		}

		&.margin-based-transition-170 {
			min-width: 170px;
		}

		&.margin-based-transition-250 {
			min-width: 250px;
		}

		&.margin-based-transition-350 {
			min-width: 350px;
		}

		@include atXS {
			font-size: 16px;
			font-weight: 700;

			&.margin-based-transition-120 {
				min-width: 84px;
			}

			&.margin-based-transition-170 {
				min-width: 119px;
			}

			&.margin-based-transition-250 {
				min-width: 175px;
			}

			&.margin-based-transition-350 {
				min-width: 300px;
			}
		}


		&--processing {
			cursor: default;
			pointer-events: none;
			color: transparent;
			-webkit-user-select: none;
			user-select: none;


			&.dp-controls__button--save {
				background-color: rgba(var(--dpu-color-b), 1);
			}

			.dp-running-bullets-anim.dp-controls__running-bullets-anim {
				top: 50%;
				transform: translate(-50%, -50%);
				//top: calc(50% + 2px);

				ul {
					flex-wrap: nowrap;
					flex-shrink: 0;

					li {
						background-color: rgba(var(--dpu-color-b-text), 0.25) !important;
						transition: all 0.15s ease-in-out;
						width: 6px;
						height: 6px;

						&.semi-active {
							transform: translateY(-5%);
							background-color: rgba(var(--dpu-color-b-text), 0.918) !important;
						}

						&.active {
							background-color: rgba(var(--dpu-color-b-text), 1) !important;
							transform: translateY(-10%);
						}
					}
				}
			}

		}

		&--processed {
			cursor: default;
			color: rgba(var(--dpu-color-g-text), 1);
			background-color: rgba(var(--dpu-color-g), 1);
			pointer-events: none;
			//animation: pulse 1s; 

		}

		&--error {
			cursor: default;
			pointer-events: none;
			//animation: pulse 1s; 
			background-color: rgba(var(--dpu-color-r), 1);
		}
	}

	&__hint {
		opacity: 0.95;
		font-size: 18px;
		pointer-events: none;
		text-transform: uppercase;
		background-color: rgba(255, 255, 255, 0.733);
		font-weight: bold;
		text-align: center;
		padding: 10px;
		display: none;
		max-width: 300px;

		&--active {
			animation: pulse 2s infinite;
		}
	}


	&--top {

		.dp-controls__wrap {
			bottom: unset;
			top: calc(30px + 2vw);
			//right: 108px;  
			align-items: flex-start;
			padding-top: 10px;

			@include atXS {
				top: 50px;

			}

			@include atXXS {
				right: 10px;
			}
		}

		.dp-controls__multi-close {
			position: relative;
			margin: 0 !important;
			transform: translateY(-10px);

			@include atM {
				transform: translateY(-20px);
			}

			@include atXS {
				min-height: 44px;
			}

			.dp-button-cancel {
				@include atXS {
					width: 44px;
					height: 44px;
				}


				border-radius: 2px;

				&--alt {
					border-radius: 50%;
				}
			}

			.dp-button-cancel-sub {
				border-radius: 2px;
			}
		}
	}
}