@import '../../../../../../../includes/assets/css/consts.scss';

.dp-button-cancel-sub {
	min-width: 150px;
	margin-top: 10px;
	cursor: pointer;
	padding: 10px 10px 10px 14px;
	border-radius: 0px;
	transition: color .8s ease-in-out, box-shadow 0.3s ease-in-out, background-color .5s ease-in-out, transform 300ms ease-in-out, opacity 300ms ease-in-out, margin-right 300ms ease-in-out;
	@include dpitBoxShadow;
	outline: none;
	border: none;
	//z-index: 2147483644;
	@include z-index(13);
	background-color: rgba(var(--dpu-color-1-light), 0.97);
	@include secondaryFont(700, $text-color-dark, 14px, 1.5, normal, right);
	@include activeTransitionFade;

	&:hover {
		transform: scale(1.05);
		background-color: rgba(var(--dpu-color-1-light), 1);
	}

	&--semi-active {
		color: rgba($text-color-dark, 0.65);
	}

	&--slower-opacity {
		transition: color .8s ease-in, background-color .5s ease-in, transform $transition-time-fast ease-in, opacity $transition-time-med ease-in;

		&.dp-button-cancel-sub--exiting {
			transition: color .8s ease-in, background-color .5s ease-in, transform $transition-time-fast ease-in, opacity 250ms ease-in;
		}
	}

}