@import '../../../../../../../includes/assets/css/consts.scss';


@mixin activeSlideUp($padding: 0, $margin: 0) {
	opacity: 0;
	height: 0;
	pointer-events: none;
	transition: height 0.3s ease-in-out, width 0.3s ease-in-out, opacity 0.3s linear, padding 0.3s linear, background-color 0.3s ease-in-out;

	&--active {
		opacity: 1;

		@if $padding {
			padding: $padding;
		}

		@if $margin {
			margin: $margin;
		}

		height: unset;
		pointer-events: all;
	}
}

@mixin activeSlideLeft($padding, $margin, $opacity: 1) {
	opacity: 0;
	width: 0;
	height: 0;
	pointer-events: none;
	transition: width 0.3s ease-in-out, opacity 0.3s ease-in-out, padding 0.3s ease-in-out, transform 0.3s ease-in-out;

	&--active {
		opacity: 1;

		@if $padding {
			padding: $padding;
		}

		@if $margin {
			margin: $margin;
		}

		@if $opacity {
			opacity: $opacity;
		}

		width: unset;
		height: unset;
		pointer-events: all;
	}
}

.dp-target-sel-ui {
	display: flex;
	flex-direction: column;
	width: 300px;
	padding: 0;

	@include atXXS {
		width: 90vw;
	}

	&__control {
		display: flex;
		padding: 10px 10px 12px;
		background: linear-gradient(315deg, rgba(var(--dpu-color-2), 0.7) 25%, rgba(var(--dpu-color-2), 0.78) 100%);
		flex-direction: row-reverse;
		align-items: center;

		@include atS {
			flex-direction: row;
		}

		&__item {
			margin: 0 8px;
		}

		&__icon {
			flex-shrink: 0;

			svg {
				width: auto;
				height: 35px;
				margin: -4px 8px;
				transition: box-shadow 0.3s ease-in-out, opacity 300ms ease-in-out;
				opacity: 1;
			}
		}

		&__skip.toggleContainer {
			// skip on desktop/mobile
			position: relative;
			background-color: rgba(#ffffffd2, 0.45);
			border-radius: 2px;
			@include panelControlItemHovFoc($transition-time-fast);
			display: flex;

			&.toggleContainer--disabled {
				opacity: 0.4;
				pointer-events: none;
			}

			.toggleOption {
				box-sizing: border-box;
				display: inline-block;
				position: relative;
				z-index: 1;
				text-align: center;

				cursor: pointer;
				transition: all $transition-time-fast ease-in-out;
				@include primaryFont(300, rgba(var(--dpu-color-g-text), 1), 11px, 1, normal, center);
				white-space: nowrap;
				opacity: 0.65;
				padding: 8px 8px;

				@include atXS {
					//padding: 11px 10px 11px 14px;  
				}

				&:nth-child(2) {
					//padding-left: 10px;
					padding-right: 17px;
					color: rgba(var(--dpu-color-r-text), 1);
				}

				&.selected {
					opacity: 1;
					font-weight: 600;
					cursor: default;
				}
			}

			.toggle {
				position: absolute;
				border-radius: 2px;
				height: 100%;
				top: 0;
				left: 0;
				transition: all $transition-time-fast ease-in-out;
				background-color: rgba(80, 80, 80, 0.555);

				&.on {
					background: rgba(var(--dpu-color-g), 0.9);
					width: 51.0% !important;
				}

				&.off {
					background: rgba(var(--dpu-color-r), 0.9);
					width: 53.2% !important;
					transform: translateX(90.98%) !important;
				}
			}
		}

		&__label {
			text-transform: uppercase;
			@include primaryFont(500, rgba(var(--dpu-color-2-text), 1), 13px, 1, 1.5px, right);
		}

	}

	&__main {
		padding: 10px 12px;

		&__title {
			text-align: center;
			@include activeSlideUp(0 0 3px 0, -5px 0 0 0);
			//font-variant: small-caps;
		}

		&__row {
			display: flex;
			justify-content: center;
			align-items: center;
			padding-bottom: 3px;
		}

		&__trash {
			cursor: pointer;
			display: none;
			width: 15px;
			height: 15px;
			margin-left: 5px;
			margin-top: -2px;
			transition: transform $transition-time-faster ease-in-out;

			&--active {
				display: block;
			}


			&:hover {
				transform: scale(1.05);
				opacity: 1;
			}

			svg {
				width: 14px;
				height: 14px;

				path {
					transition: fill $transition-time-faster ease-in-out;
					fill: rgba(var(--dpu-color-r), 0.9);
				}
			}
		}

		&__button {
			display: flex;
			align-items: center;
			justify-content: center;
			//flex-basis: 100%;
			border-radius: 2px;
			background-color: rgba(var(--dpu-color-b), 0.9);
			cursor: pointer;

			@include activeSlideUp(8px 30px, 0 0 6px 0);

			&__icon {
				width: 22px;
				height: 22px;
				margin: -3px 3px -1px 0;
				transition: transform 0.3s ease-in-out;

				svg {

					path {
						transition: fill 0.3s ease-in-out;
						fill: rgba(var(--dpu-color-b-text), 1);
					}
				}
			}

			&__label {
				margin-left: 3px;
				@include primaryFont(600, rgba(var(--dpu-color-b-text), 1), 16px, 1, 1px);
				text-transform: uppercase;
				padding: 3px 0;
			}




			&:hover {
				background-color: rgba(var(--dpu-color-b), 1);

				.dp-target-sel-ui__main__button__icon {
					transform: rotate(90deg) scale(0.95);
				}
			}

			&--half {
				margin-left: 3px;
				margin-right: 3px;
				flex-basis: unset;

				&.dp-target-sel-ui__main__button--active {
					padding: 10px 20px;
				}

				.dp-target-sel-ui__main__button__icon {
					width: 16px;
					height: 16px;
				}

				.dp-target-sel-ui__main__button__label {
					font-size: 12px;
					text-align: left;
					font-weight: 600;
				}

				&:hover {
					background-color: rgba(var(--dpu-color-b), 1);

					.dp-target-sel-ui__main__button__icon {
						transform: scale(1.05);
					}
				}
			}

		}

		&__value {
			font-size: 12px;
			font-weight: 600;
			text-overflow: ellipsis;
			line-height: 1;
			display: block;
			max-width: calc(90% - 40px);

			overflow: hidden;
			white-space: nowrap;
			color: rgba($text-color-dark, 1);

			&--no-target {
				text-overflow: unset;
				margin: 0 auto;
				text-align: center;
				overflow: unset;
				white-space: normal;
				color: rgba($text-color-dark, 0.9);
				font-weight: 400;
			}

			&--skipped {
				font-weight: 400;
				text-overflow: unset;
				margin: 0 auto;
				text-align: center;
				overflow: unset;
				white-space: normal;
				color: rgba(var(--dpu-color-r), 1);
			}
		}
	}
}