@import '../../../../../../../includes/assets/css/consts.scss';

$activeElColorSemiDark: rgba(var(--dpu-color-2), 0.95);

@mixin disabled-ctrl-item {
	opacity: 0.35;
}

.dp-step-edit {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	padding-bottom: 10px; // react on mouse enter was not triggered on the border
	margin-bottom: -10px;

	&__wrap {
		display: flex;
		width: 100%;
		justify-content: space-between;
		align-items: flex-start;

		@include atXXXS {
			flex-direction: column-reverse;
			align-items: stretch;
		}
	}

	&__ta-wrap {
		display: flex;
		flex-direction: column;
		//flex-basis: 260px;
		flex: 1 1 auto;
	}

	&__ctrls-2 {
		//z-index: 2147483644;
		@include z-index(13);
		padding-right: 30px;

		@include atXXXS {
			padding-left: 30px;
		}

		&__item {
			display: flex;
			flex-direction: column;
			align-items: flex-start;

			@include atXXXS {
				flex-direction: row;
				align-items: center;
			}
		}

		.dp-step-edit__label {
			padding: 0 0 5px;

			@include atXXXS {
				padding: 0 5px 5px 0;
			}
		}
	}

	&__ctrls {
		//z-index: 2147483646;
		@include z-index(15);
		margin-top: 10px;
		margin-bottom: 20px;
		position: relative;
		display: flex;
		background: $activeElColorSemiDark;
		background: linear-gradient(315deg, rgba(var(--dpu-color-2), 0.70) 25%, rgba(var(--dpu-color-2), 0.78) 100%);
		box-shadow: inset -5px -2px 5px rgba(0, 0, 0, 0.055);
		border-bottom-right-radius: 2px;
		border-top-right-radius: 2px;
		padding: 15px 35px 0px 35px;
		flex-wrap: wrap;

		@include atXS {
			padding: 10px 10px 0px 15px;
			margin-right: 25px;
			margin-bottom: 15px;
		}

		@include atXXXS {
			margin-right: 0px;
		}

		&__item {
			width: auto;
			margin-top: 10px;
			margin-right: 20px;
			display: flex;
			flex-direction: column;
			justify-content: flex-start;
			align-items: center;
			margin-bottom: 7px;

			&:first-child {
				@include z-index(13);
				//z-index: 2147483644;
			}

			&:nth-child(2) {
				//z-index: 2147483644;
				@include z-index(13);
				padding-right: 5px;
			}

			@include atXS {
				margin-right: 10px;
				justify-content: flex-start;
			}

			@include atXXS {
				transform: none;
			}

			@include atXXXS {
				margin-right: 7px;
			}

			&__label {
				transition: opacity 0.3s ease-in-out;
				@include primaryFont(300, rgba($text-color-light, 0.95), 14px, 1.14, normal, left);
				text-transform: uppercase;
				vertical-align: middle;
				padding-bottom: 3px;
				cursor: default;
				margin-bottom: 2px;

				&--dimmed {
					opacity: 0.5;
				}

				@include atXS {
					font-size: 13px;
				}

				@include atXXS {
					text-transform: unset;
					font-size: 14px;
				}
			}
		}
	}

	&__mob-type-icon {
		path {
			transition: fill 0.3s ease-in-out;
			fill: rgba(var(--dpu-color-g), 1);
		}

		&--no-target {
			path {
				fill: rgba(var(--dpu-color-2-text), 0.7);
			}
		}

		&--skipped {
			path {
				fill: rgba(var(--dpu-color-r), 1);
			}
		}

		&--clickable {
			cursor: pointer;
		}
	}

	&__mob-type-box {
		box-shadow: 0 0 6px 0 rgba(var(--dpu-color-g), 0.5);

		&--no-target {
			box-shadow: 0 0 6px 0 rgba(var(--dpu-color-2-text), 0.9);
		}

		&--skipped {
			box-shadow: 0 0 6px 0 rgba(var(--dpu-color-r), 0.5);
		}
	}

	&__label {
		@include primaryFont(500, $text-color-dark, 10px, 17px, 0.83px, left);
		text-transform: uppercase;
		@include pullOutPanelPadding(0, 0);
		margin: 0;

		@include atXXS {
			//display: none;
		}

		&__icon {
			display: inline-block;
		}
	}


	&__intro-text {
		cursor: pointer;
		align-self: stretch !important;
		padding: 10px 20px !important;
		margin: 5px 20px 34px 30px !important;
		border-radius: 2px !important;
		transition: all 0.3s ease-in-out !important;
		outline: none !important;
		border: none !important;
		min-width: unset !important;
		max-height: 100px;
		resize: none;


		background: rgba(#fff, 0.8) !important;
		box-shadow: 0px 0px 5px 0px rgba($text-color-dark, 0.3) !important;
		@include primaryFontImportant(400, $text-color-dark, 14px, 16px, normal);

		&:focus {
			box-shadow: 0px 0px 10px 3px rgba(var(--dpu-color-2), 0.3) !important;
			background: #fff !important;
		}

		@include atXXS {
			margin: 5px 20px 25px !important;
			width: unset !important;

			&--higher-on-mobile {
				margin-bottom: 150px !important;
			}
		}
	}

	&__signals {
		display: flex;
		justify-content: center;
		margin-top: 3px;
		margin-left: -2px;


		&--disabled {
			@include disabled-ctrl-item;
		}
	}

	&__signal-icon:nth-child(2) {
		padding: 0 2px;
		z-index: 0;
	}


	&__multi-container {
		@include z-index(11);
		//z-index: 2147483642;
		min-width: 50px;
		margin-top: -7px;

		.dp-multi-container__menu {
			padding: 4px;
			transform-origin: calc(100% - 27px) 27px;

			.dp-multi-container__item {
				.dp-button-cancel-sub {
					padding: 0;
					overflow: hidden;
					min-width: unset;
					cursor: default;

					@include atS {
						text-align: left;
						background-color: rgba(var(--dpu-color-1-light), 1);
					}

					@include atXXS {
						min-width: 180px;
						transform: none;
					}
				}

				&:nth-child(2) {
					.dp-button-cancel-sub {
						margin-top: 5px;
					}
				}
			}

			@include atS {
				transform-origin: 27px 27px;
				right: unset;
				left: 0;
				align-items: flex-start;
			}

			@include atXS {

				transform-origin: 27px 27px;
			}

			@include atXXS {
				transform: none;
			}
		}
	}

	&__arrow-down {
		transition: opacity $transition-time-fast ease-in-out, height $transition-time-fast ease-in-out;
		width: 22px;
		height: 22px;
		opacity: 0.85;
		cursor: pointer;
		pointer-events: all;
		align-self: flex-end;
		margin-right: 10px;

		@include atXS {
			margin-left: 10px;
			margin-right: 0px;
		}

		&--hidden {
			height: 0;
			width: 0;
			opacity: 0;
			pointer-events: none;
		}

		&--up {
			transform: rotate(180deg);
		}

		&--l {
			width: 27px;
			height: 27px;
		}

		&:hover {
			opacity: 1;
		}

		path {
			transition: stroke $transition-time-fast ease-in-out;
			stroke: rgba(var(--dpu-color-2-text), 1);
		}

	}

	&__button {

		cursor: pointer;
		background-color: rgba(#000, 0);
		opacity: 0.95;
		background-position: center;
		background-size: 80%;
		background-repeat: no-repeat;
		width: 46px;
		height: 46px;
		border: none;
		outline: none;
		transition: all 0.3s ease;

		&--target {
			border-radius: 50%;
			background-image: url('./../../../../../icons/dp-crosshairs-target.svg');
		}


		&:hover {
			opacity: 1;
		}

		&--url {
			background-image: url('./../../../../../icons/directions-solid.svg');
			fill: #fff;
			margin-top: -4px;

			&:hover {
				transform: scale(1.1) rotate(-10deg);
			}
		}

		&--disabled {
			@include disabled-ctrl-item;
		}
	}

	&__sub-button {
		margin-top: 7px;
		min-width: 180px;
	}

	// interaction
	&__toggle.toggleContainer {

		@include z-index(14);
		position: relative;
		background-color: rgba(#ffffffd2, 0.45);
		border-radius: 2px;
		min-width: 96px;

		@include panelControlItemHovFoc($transition-time-fast);
		display: flex;


		&.toggleContainer--disabled {
			@include disabled-ctrl-item;
			pointer-events: none;
		}


		.toggleOption {
			box-sizing: border-box;
			display: block;
			position: relative;
			z-index: 1;
			flex-basis: 50%;
			cursor: pointer;
			transition: all $transition-time-fast ease-in-out;
			@include primaryFont(300, rgb(var(--dpu-color-g-text)), 14px, 14px, normal, center);
			white-space: nowrap;
			opacity: 0.65;
			padding: 13px 8px 13px 10px;

			@include atXS {
				//padding: 11px 10px 11px 14px;
			}

			&:nth-child(2) {
				padding-left: 10px;
				padding-right: 28px;
				color: rgb(var(--dpu-color-r-text));
			}

			&.selected {
				opacity: 1;
				font-weight: 600;
				cursor: default;
			}
		}





		&.toggleContainer--half-size {
			.toggleOption {
				padding: 4px 10px;
				font-size: 10px;
			}
		}

		&.toggleContainer--dark-bg {
			background-color: $text-color-placeholder;
		}

		&.toggleContainer--hidden {
			opacity: 0;
			pointer-events: none;
			height: 0;

			.toggleOption {
				font-size: 0;
			}
		}

		.toggle {
			position: absolute;
			border-radius: 2px;
			height: 100%;
			top: 0;
			left: 0;
			transition: all $transition-time-fast ease-in-out;
			background-color: rgba(80, 80, 80, 0.555);

			&.on {
				background: rgba(var(--dpu-color-g), 0.95);
				width: 54.0% !important;
			}

			&.off {
				background: rgba(var(--dpu-color-r), 0.95);
				width: 54.2% !important;
				transform: translateX(84.98%) !important;
			}
		}

	}

	&__select {
		display: inline-block;
		width: auto;
		min-width: 150px;
		min-height: unset;
		height: 40px;


		&:not(.dp-select__native) {
			@include atXS {
				min-width: 100px;
				padding-top: 5px;
			}
		}

		&.dp-select__native {
			@include atXS {
				min-width: 100px;
			}

			@include atXXS {
				min-width: 70px;
			}
		}

		&--highlight {
			min-width: 120px;

			&:not(.dp-select__native) {
				@include atXXXS {
					min-width: 150px;
					padding-top: 5px;
				}
			}

			&.dp-select__native {
				@include atXXXS {
					min-width: 150px;
				}
			}


		}



	}

	&__navig {
		margin-left: auto;
		margin-right: 0;
		display: flex;
		justify-content: flex-end;
		transform: skew(-1deg, 0deg);

		@include atXS {
			justify-content: stretch;
			margin-left: 0;
			width: 100%;
			transform: none;
		}

		&__button {
			cursor: pointer;
			display: flex;
			align-items: center;
			justify-content: center;
			color: rgba(var(--dpu-color-2-text), 1);
			background-color: rgba(var(--dpu-color-2), 1);
			padding-top: 16px;
			padding-bottom: 16px;
			min-width: 100px;
			text-decoration: none;
			transition: all $transition-time-fast ease-in-out;
			outline: none;
			border: none;

			opacity: 0.87;
			@include primaryFont(600, $text-color-light, 14px, 16px, normal, center);

			&.hovered,
			&:hover {
				opacity: 1;
			}

			@include atXS {
				min-width: 50px;
				padding-top: 15px;
				padding-bottom: 15px;
			}

			@include atXS {
				width: 25%;
			}

			@include atXXS {
				padding-top: 15px;
				padding-bottom: 15px;
				min-width: unset;
				padding-left: 20px;
				padding-right: 20px;
			}

			&--add {
				background-color: rgba(var(--dpu-color-g), 1);
				color: rgba(var(--dpu-color-g-text), 1);
			}



			&--back {
				border-top-left-radius: 2px;
				color: rgba(var(--dpu-color-2-text), 1);
				background: linear-gradient(315deg, rgba(var(--dpu-color-2), 0.75) 25%, rgba(var(--dpu-color-2), 0.82) 100%);

				&.hovered,
				&:hover {
					background: linear-gradient(315deg, rgba(var(--dpu-color-2), 0.8) 25%, rgba(var(--dpu-color-2), 0.86) 100%);
				}

				@include atXS {
					border-top-left-radius: 0px;
				}

			}

			&--next {
				color: rgba(var(--dpu-color-2-text), 1);
				background: linear-gradient(315deg, rgba(var(--dpu-color-2), 0.8) 25%, rgba(var(--dpu-color-2), 0.86) 100%);

				&.hovered,
				&:hover {
					background: linear-gradient(315deg, rgba(var(--dpu-color-2), 0.75) 25%, rgba(var(--dpu-color-2), 0.82) 100%);

				}

			}

			&--remove {
				background: linear-gradient(315deg, rgba(var(--dpu-color-r), 0.8) 25%, rgba(var(--dpu-color-r), 0.86) 100%);
				color: rgba(var(--dpu-color-r-text), 1);

				&.hovered,
				&:hover {
					background: linear-gradient(315deg, rgba(var(--dpu-color-r), 0.75) 25%, rgba(var(--dpu-color-r), 0.82) 100%);
				}

			}

			&--disabled {
				@include disabled-ctrl-item;
				cursor: default;
				pointer-events: none;
			}
		}

		&__btn-ico {
			width: 20px;

			&--remove {
				width: 22px;
			}

			&--right {
				transform: rotate(180deg);
				width: 18px;
			}

			&--left {
				width: 18px;
			}


		}
	}
}