@import '../../../../../../../includes/assets/css/consts.scss';

.dp-running-bullets-anim {
	pointer-events: none;
	position: absolute;
	display: flex !important;
	justify-content: center;
	line-height: 1;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	padding: 3px !important;
	transition: all 0.2s ease-in-out;
	opacity: 0 !important;
	//z-index: 2147483645;
	@include z-index(14);

	&.active {
		opacity: 1 !important;
	}

	ul {
		margin: 0 auto;
		padding: 0;
		display: flex;
		justify-content: center;
		flex-wrap: nowrap;
		align-items: center;

		li {
			margin: 2px;
			width: 8px;
			height: 8px;
			list-style: none;
			border-radius: 50%;
			text-decoration: none;
			outline: none;
			transform-origin: center;
			overflow: hidden;
			display: block;
			@include transitionFast(background-color);
			transition: all 0.15s ease-in-out;

			&.semi-active {
				background-color: rgba(255, 255, 255, 0.918);
			}

			&.active {
				background-color: #fff;
			}
		}
	}
}

