@import '../../../../../../../includes/assets/css/consts.scss';

@mixin active {
	opacity: 1;
	transform: translate(-50%, -50%);

	@include atXS {
		transform: translate(-50%, -50%);
	}

	pointer-events: all;
}

@mixin inactive {
	opacity: 0;
	transform: translate(150%, -100%);

	@include atXS {
		transform: translate(150%, -100%);
	}

	pointer-events: none;
}

.dp-promo-popup {
	position: fixed;
	//z-index: 2147483644;
	@include z-index(13);
	min-width: 500px;

	top: 50%;
	left: 50%;
	background-color: rgb(255, 255, 255);
	padding: 7px;
	padding-top: 37px;
	transition: all 300ms ease-in-out;
	border-radius: 4px;
	@include primaryFont(400, $text-color-dark, 14px, 1.5em);

	@include inactive;
	text-align: right;



	@include atXS {
		min-width: 80%;

	}

	&--dismissible {
		padding-top: 7px;

	}

	&__content {
		padding: 10px 43px 15px;
		margin-top: -30px;
		display: flex;
		flex-direction: column;
		align-items: center;

		@include atS {
			padding: 10px 25px 15px;

		}

		&__image {
			width: 70%;
			max-width: 200px;
			height: auto;
			text-align: center;
			margin-bottom: 10px;
		}

		&__title {
			text-align: center;
			font-size: 18px;
			text-transform: uppercase;
			font-weight: bold;
		}

		&__desc {
			font-size: 14px;
			text-align: center;
			margin-bottom: 0;
			line-height: 1.3em;
			margin: 10px auto 0;
		}
	}


	&__buttons {
		display: flex;

		@include atXS {
			flex-direction: column;
		}

		&__btn {
			display: inline-block;
			margin-top: 10px;
			cursor: pointer;
			padding: 11px 20px 9px;
			border-radius: 2px;
			transition: all $transition-time-fast ease-in;
			@include dpitBoxShadow;
			outline: none;
			border: none;
			//z-index: 2147483644;
			@include z-index(13);
			@include secondaryFont(700, $text-color-dark, 14px, 1.5, normal, center);

			&:hover {
				transform: scale(1.05);
			}

			&--yes {
				margin-right: 5px;
				background-color: rgba(var(--dpu-color-g), 0.788);
				color: rgba(var(--dpu-color-g-text), 1);

				&:hover {
					background-color: rgba(255, 205, 3, 0.904);
					color: $text-color-dark;
				}

				@include atXS {
					margin-right: 0;
				}
			}

			&--no {
				margin-left: 5px;
				color: $text-color-dark;
				opacity: 0.9;

				&:hover {
					opacity: 1;
				}

				@include atXS {
					margin-left: 0;
				}
			}
		}
	}

	&__transition {
		&-enter-done {
			@include active;
		}

		&-exit {
			transform: translate(-150%, 100%);
		}
	}

	&__close-button {
		margin-right: 5px;
		margin-top: 3px;
		margin-left: auto;
	}
}