@import '../../../../../../../includes/assets/css/consts.scss';

.dp-target-selector {
	//z-index: 2147483647 !important;
	@include z-index(16, true);

	display: none;
	opacity: 0 !important;
	position: absolute;
	background: rgba(var(--dpu-color-3), 1);
	outline: 2px solid #333;
	pointer-events: none;
	//border-radius: 5px;
	box-shadow: 0 0 15px 10px rgba(0, 0, 0, .8);
	transition: top 0.02s ease-in, left 0.02s ease-in, width 0.02s ease-in, height 0.02s ease-in, opacity 0.3s ease-in-out;

	&--confirmed {
		animation: pulse 1s;
		//z-index: 2147483640 !important; 
		@include z-index(10, true);
		color: #d29e68;
	}

	&--orange {
		background: rgba(var(--dpu-color-4), 1);
	}

	&--yellow {
		background: rgba(var(--dpu-color-2), 1);
	}

	&--active {
		opacity: 0.5 !important;
	}

}

.dp-visualized-target {
	cursor: pointer;
}